<template>
  <div id="down">
    <transition name="buffs">
      <div class="buffs" v-if="wrap && title === '내 포켓몬'">
        <b-form-group label="캐릭터 버프/디버프:" v-slot="{ ariaDescribedby }">
          <b-form-checkbox-group id="checkbox-group-2"
            :aria-describedby="ariaDescribedby"
            name="buffs"
            @change="addBuffs"
          >
            <b-form-checkbox :key="index" v-for="(buffs, index) in $store.state.buffs" :value="index">{{buffs.name}}</b-form-checkbox>
          </b-form-checkbox-group>
        </b-form-group>
      </div>
      <div class="buffs" v-if="wrap && title !== '내 포켓몬'">
        <b-form-group label="캐릭터 버프/디버프:" v-slot="{ ariaDescribedby }">
          <b-form-checkbox-group id="checkbox-group-2"
            :aria-describedby="ariaDescribedby"
            name="debuffs"
            @change="addDebuffs"
          >
            <b-form-checkbox :key="index" v-for="(debuffs, index) in $store.state.debuffs" :value="index">{{debuffs.name}}</b-form-checkbox>
          </b-form-checkbox-group>
        </b-form-group>
      </div>
    </transition>
      <b-button class="wrap" variant="info" @click="turnWrap" v-if="!wrap">고급설정</b-button>
      <b-button class="wrap" variant="info" @click="turnWrap" v-if="wrap">숨기기</b-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      wrap: false
    }
  },
methods: {
    turnWrap() {
      this.wrap = !this.wrap;
    },
    addBuffs(value) {
        this.$store.commit("changeBuffs", value);
      },
    addDebuffs(value) {
        this.$store.commit("changeDebuffs", value);
      }
  },
  props: ['title']
}
</script>

<style scoped>
#down {
  width: 100%;
  padding: 1rem;
  margin: 0;

  border-top: 1px solid gray;
}

.wrap {
  display: block;
  font-size: 0.8rem;
  margin: 0.3rem auto;
}

.buffs-enter,
.buffs-leave-to {
  visibility: hidden;
  height: 0;
  margin: 0;
  padding: 0;
  opacity: 0;
}

.buffs-enter-active,
.buffs-leave-active {
  transition: all 0.5s;
}
</style>