import { GetPokemonList, GetPokemonInfo } from '../api/index'

export const actions = {
    GET_PKLIST(ctx) {
        GetPokemonList()
            .then(res => {
                ctx.commit("updatePokemonList", res.data);
            }).catch(err => {
                console.log(err);
            });
    },
    GET_PKINFO(ctx, payload) {
        GetPokemonInfo(payload[0])
            .then(res => {
                ctx.commit('updatePokemonInfo', [res.data, payload[1]]);
            }).catch(err => {
                console.log(err);
            });
    },
    CAL_DAMAGE(ctx) {
        let usingSkill = ctx.state.skill[ctx.state.selected.myskill];
        if (usingSkill.canCalculate === true) {
            let returnList = [];

            if (usingSkill.damage.isMulti === true) {
                for (let usingBonusSkill of usingSkill.bonusDamage) {
                    let dmgName = usingBonusSkill.name;
                    let calculatedDmg = calculateDamage(usingBonusSkill, ctx.state.selected.mylevel, ctx.state.selected.mystats, ctx.state.selected.oppolevel, ctx.state.selected.oppostats, ctx.state.selected.mybuffs, ctx.state.selected.oppobuffs);
                    returnList.push({
                        "name": dmgName,
                        "damage": Math.floor(calculatedDmg),
                        "tick": usingBonusSkill.tick
                    });
                }
            }
            else {
                let calculatedDmg = calculateDamage(usingSkill, ctx.state.selected.mylevel, ctx.state.selected.mystats, ctx.state.selected.oppolevel, ctx.state.selected.oppostats, ctx.state.selected.mybuffs, ctx.state.selected.oppobuffs);
                returnList.push({
                    "name": "메인",
                    "damage": Math.floor(calculatedDmg),
                    "tick": usingSkill.tick
                });
                if (usingSkill.bonusDamage.length > 0) {
                    for (let usingBonusSkill of usingSkill.bonusDamage) {
                        let dmgName = usingBonusSkill.name;
                        let calculatedDmg = calculateDamage(usingBonusSkill, ctx.state.selected.mylevel, ctx.state.selected.mystats, ctx.state.selected.oppolevel, ctx.state.selected.oppostats, ctx.state.selected.mybuffs, ctx.state.selected.oppobuffs);
                        returnList.push({
                            "name": dmgName,
                            "damage": Math.floor(calculatedDmg),
                            "tick": usingBonusSkill.tick
                        });
                    }
                }
            }

            ctx.commit('setDmg', returnList);
        } else {
            ctx.commit('setDmg', []);
        }
    }
}

const addBeforeBuff = (usingSkill, myLevel, myStats, oppoLevel, oppoStats, myBuffs, myDebuffs) => {
    let usingAttack = myStats[myLevel - 1][usingSkill.damage.using];

    let usingDefense = 0;
    if (usingSkill.damage.using === "attack") {
        usingDefense = oppoStats[oppoLevel - 1]['defense'];
    } else {
        usingDefense = oppoStats[oppoLevel - 1]['sp_defense'];
    }

    for (let buff of myBuffs) {
        if (buff.before) {
            if (buff.affect[usingSkill.damage.using] === 'ShellSmash') {
                usingAttack += myStats[myLevel - 1]['defense'] * 0.4
            }
            else if (buff.affect[usingSkill.damage.using] === 'ShellSmash+') {
                usingAttack += myStats[myLevel - 1]['defense'] * 0.5
            } else {
                usingAttack *= buff.affect[usingSkill.damage.using];
            }
        }
    }

    for (let debuff of myDebuffs) {
        if (usingSkill.damage.using === "attack") {
            usingDefense *= debuff.affect['defense'];
        } else {
            usingDefense *= debuff.affect['sp_defense'];
        }
    }

    return [usingAttack, usingDefense]
}

// const addLaterBuff = (calculatedDmg,) => {

// }

const calculateDamage = (usingSkill, myLevel, myStats, oppoLevel, oppoStats, myBuffs, myDebuffs) => {
    if (usingSkill.damage.isHPRatio) {
        return Math.floor(usingSkill.damage.HPRatio * oppoStats[oppoLevel - 1].hp);
    }

    let tempArray = addBeforeBuff(usingSkill, myLevel, myStats, oppoLevel, oppoStats, myBuffs, myDebuffs)
    let usingAttack = tempArray[0];
    let usingDefense = tempArray[1];

    let calculatedDmg = Math.floor(usingSkill.damage.attackRatio * usingAttack + usingSkill.damage.levelRatio * (myLevel - 1) + usingSkill.damage.bonus);
    // addBuff(calculatedDmg,)

    if (!usingSkill.damage.noDefense && oppoStats.length > 1) {
        if (usingSkill.damage.using === "attack") {
            calculatedDmg = calculatedDmg * 600 / (600 + usingDefense);
        } else {
            calculatedDmg = calculatedDmg * 600 / (600 + usingDefense);
        }
    }

    return calculatedDmg;
}