import Vue from 'vue'
import Vuex from 'vuex'

import {state} from './state'
import {actions} from './actions'

Vue.use(Vuex);

export const store = new Vuex.Store({
    state,
    getters: {

    },
    actions,
    mutations: {
        updatePokemonList(state, data) {
            state.PokemonList = data.list
        },
        updatePokemonInfo(state, payload) {
            if (payload[1]) {
                state.canCalculate = payload[0].canCalculate;
                state.skill = payload[0].skill;
                state.buffs = payload[0].buffs;
                state.selected.mystats = payload[0].stats;
            } else {
                state.selected.oppostats = payload[0].stats;
                state.debuffs = payload[0].debuffs;
            }
            this.dispatch("CAL_DAMAGE");
        },
        updateMySelectedSkill(state, value) {
            state.selected.myskill = value;
            this.dispatch("CAL_DAMAGE");
        },
        updateMySelectedLevel(state, value) {
            state.selected.mylevel = value;
            this.dispatch("CAL_DAMAGE");
        },
        updateOppoSelectedLevel(state, value) {
            state.selected.oppolevel = value;
            this.dispatch("CAL_DAMAGE");
        },
        updateMySelectedPokemon(state, value) {
            state.selected.mypokemon = value;
            let idx = state.PokemonList.indexOf(value);
            this.dispatch("GET_PKINFO", [idx, true]);
        },
        updateOppoSelectedPokemon(state, value) {
            state.selected.oppopokemon = value;
            let idx = state.PokemonList.indexOf(value);
            this.dispatch("GET_PKINFO", [idx, false]);
        },
        setDmg(state, value) {
            state.damage = value;
            state.maxdamage = 0;
            ("state.damage", state.damage);
            for (let dmg of state.damage) {
                state.maxdamage += dmg.damage * dmg.tick;
            }
        },
        updateInitial() {
            let idx = 0;
            this.dispatch("GET_PKINFO", [idx, true]);
        },
        changeBuffs(state, value) {
            state.selected.mybuffs = [];
            for (let i of value) {
                state.selected.mybuffs.push(state.buffs[i]);
            }
            this.dispatch("CAL_DAMAGE");
        },
        changeDebuffs(state, value) {
            state.selected.oppobuffs = [];
            for (let i of value) {
                state.selected.oppobuffs.push(state.debuffs[i]);
            }
            this.dispatch("CAL_DAMAGE");
        }
    }
});